import React, { Component } from 'react';
import BreadCrumb from "../../../common/bread";
import {Input, Button, Table} from "antd";
import { connect } from "react-redux";
import { Link } from  "react-router-dom";
import { actionCreators } from "./store";

class groupMessage extends Component {
  render() {
    const { gonghaoList, name, search, getMaterial, changeSearchInput } = this.props;
    const gonghaoHeader = [
      {
        key: "code",
        title: "code",
        dataIndex: "code",
        align: "center"
      }, {
        key: "name",
        title: "公号名称",
        dataIndex: "name",
        align: "center"
      }, {
        key: "Action",
        title: "Action",
        align: "center",
        width: 400,
        render: (text, record) => (
          <div>
            <Button size="small" type="dashed" onClick={() => getMaterial(record.code)}>同步素材</Button>
            <Button size="small" type="primary" style={{margin: "0 10px"}}>
              <Link to={"/admin/message/group/material/" + record.code}>群发消息</Link>
            </Button>
            <Button size="small">
              <Link to={"/admin/message/group/history/" + record.code}>群发记录</Link>
            </Button>
          </div>
        )
      }
    ];
    return (
    	<div>
        <BreadCrumb/>
        <div className="container">
          <Input.Search
            placeholder="请输入公号名称"
            enterButton="Search"
            style={{width: 300, marginBottom: 20}}
            onSearch={search}
            value={name}
            onChange={changeSearchInput}
          />
          <Table size="small" rowKey={record => record._id} columns={gonghaoHeader} dataSource={gonghaoList}  />
        </div>
    	</div>
    )
  }

  componentDidMount() {
    this.props.getGroupGonghao()
  }
}

const mapState = state =>({
  gonghaoList: state.message.group.gonghaoList,
  name: state.message.group.name
});

const mapDispatch = dispatch => ({
  getGroupGonghao() {
    dispatch(actionCreators.getGroupGonghao())
  },
  search(name) {
    dispatch(actionCreators.search(name))
  },
  changeSearchInput(e) {
    dispatch(actionCreators.changeSearchInput(e.target.value))
  },
  getMaterial(code) {
    dispatch(actionCreators.getMaterial(code))
  }
});

export default connect(mapState, mapDispatch)(groupMessage);