import React, { Component } from 'react';
import BreadCrumb from "../../../../common/bread";
import {Button, Modal, Table, Tooltip, Input} from 'antd';
import moment from 'moment';
import {connect} from "react-redux";
import { actionCreators } from '../store';


class History extends Component {
  componentWillMount() {
    const { code } = this.props.match.params;
    this.props.getTagList(code);
    this.props.getHistoryList(code);
  }

  render() {
    const { history, historyList, tagList, checkSendState, match, openMsgModal, closeMsgModal, deleteMsg, changeArticleIdx, delMsgInfo, historyMsgModal, clearHistory } = this.props;
    const { code } = match.params;
    const historyHeader = [
      {
        key: "update_time",
        title: "发送消息时间",
        align: "center",
        width: 100,
        render: (text, record) => {
          let time = moment(record.update_time * 1000).format("YYYY-MM-DD, HH:mm:ss");
          return (
            <span>{time}</span>
          )
        }
      },
      {
        title: "发送对象",
        align: "center",
        render: (text, record) => {
          let tag = "无";
          if(record.tagId) {
            tag = tagList.map(item => {
              return (record.tagId === item.id) && (tag = item.name);
            });
          }
          return (
            <div>
              {tag}
            </div>
          )
        }
      },
      {
        title: "是否成功",
        align: "center",
        width: 200,
        render: (text, record) => {
          let msg_id = record.msg_id;
          let success;
          if(msg_id === "") {
            success = "无msg_id,发送不成功"
          } else {
            success = "msg_id存在,发送成功"
          }
          return <span>{success}</span>
        }
      },
      {
        title: "图文标题",
        align: "center",
        width: 300,
        render: (text, record) => {
          let arr =
            record.content.news_item.length !== 0
              ? record.content.news_item
              : [];
          let titles = [];
          for (let i = 0; i < arr.length; i++) {
            titles.push(i + 1 + "." + arr[i].title);
          }
          return (
            <Tooltip placement="top" title={titles}>
              <p className="textEllipsis" style={{width: 300}}>{titles}</p>
            </Tooltip>
          )
        }
      },
      {
        key: "isTiming",
        title: "是否定时",
        align: "center",
        render: (text, record) => {
          let time = record.isTiming ? "是" : "否";
          return (
            <span>{time}</span>
          )
        }
      },
      {
        key: "timing",
        title: "定时时间",
        align: "center",
        render: (text, record) => {
          let time = "~~";
          if (record.timing) {
            time = moment(record.timing).format("YYYY-MM-DD, HH:mm:ss")
          }
          return (
            <span>{time}</span>
          )
        }
      },
      {
        title: "Action",
        align: "center",
        width: 100,
        render: (text, record) => (
          <div>
            <Button size="small" type="primary" style={{marginBottom: 10}} onClick={() => checkSendState(code, record.msg_id)}>发送状态</Button>
            <Button size="small" type="danger" onClick={() => openMsgModal()}>删除消息</Button>
          </div>
        )
      }
    ];
    return (
    	<div>
        <BreadCrumb/>
        <div className="container">
            <Button style={{margin: "0 20px 20px 0"}} type="danger" onClick={() => clearHistory(code)}>清空列表</Button>
            <Button onClick={() => history.goBack()}>返回上一级</Button>
          <Table size="small" bordered rowKey={record => record._id} columns={historyHeader} dataSource={historyList} />
        </div>
        <Modal
          title="请输入要删除的消息的索引"
          visible={historyMsgModal}
          onOk={() => deleteMsg(delMsgInfo)}
          onCancel={closeMsgModal}
        >

          <Input style={{width: 100}} value={delMsgInfo.article_idx} onChange={changeArticleIdx} />
          <p style={{color: "red", marginTop: 10}}>Tips:   0为全部删除  1、2、3...为对应第几条</p>
        </Modal>
    	</div>
    )
  }
}

const mapState = state => ({
  tagList: state.message.group.tagList,
  historyList: state.message.group.historyList,
  historyMsgModal: state.message.group.historyMsgModal,
  delMsgInfo: state.message.group.delMsgInfo
});

const mapDispatch = dispatch => ({
  getHistoryList(code) {
    dispatch(actionCreators.getHistoryList(code))
  },
  getTagList(code) {
    dispatch(actionCreators.getTagList(code))
  },
  checkSendState(code, msg_id) {
    dispatch(actionCreators.checkSendState(code, msg_id))
  },
  openMsgModal(code, msg_id) {
    dispatch(actionCreators.openMsgModal(code, msg_id))
  },
  closeMsgModal() {
    dispatch(actionCreators.closeMsgModal())
  },
  deleteMsg(delMsgInfo) {
    dispatch(actionCreators.deleteMsg(delMsgInfo))
  },
  changeArticleIdx(e) {
    dispatch(actionCreators.deleteMsg(e.target.value))
  },
  clearHistory(code) {
    dispatch(actionCreators.clearHistory(code))
  }
});

export default connect(mapState, mapDispatch)(History);
