import React, { Component } from 'react';
import { Button, Table, DatePicker, Tooltip } from "antd";
import BreadCrumb from "../../../common/bread";
import { connect } from "react-redux";
import { actionCreators } from './store';
import { Link } from "react-router-dom";

const { RangePicker } = DatePicker;

let startTime = "";
let endTime = "";

class Kefu extends Component {

  componentWillMount() {
    this.props.getKefuMessageList()
  }

  render() {
    const { kefuMessageList, copyMessage, deleteMessage, sendKefuMessage, deleteByDate, changeDate }  = this.props;
    const kefuMessageHeader = [
      {
        key: "type",
        title: "类型",
        align: "center",
        width: 50,
        render: (text, record) => (record.type === 1 ? "文本" : record.type === 0 ? "图文" : "图片") + (record.contents.length > 1 ? "(多)" : "")
      },
      {
        key: "task",
        title: "是否延时",
        align: "center",
        width: 50,
        render: (text, record) => record.task ? "是" : "否"
      },
      {
        key: "delay",
        title: "延时时间(分钟)",
        align: "center",
        width: 80,
        render: (text, record) => record.task ? record.delay : "——"
      },
      {
        key: "is_timing",
        title: "是否定时",
        align: "center",
        width: 50,
        render: (text, record) => record.is_timing ? "是" : "否"
      },
      {
        key: "timing_time",
        title: "定时时间(分钟)",
        align: "center",
        width: 100,
        sorter: (a, b) => (new Date(a.timing_time)).getTime() - (new Date(b.timing_time)).getTime(),
        render: (text, record) => record.is_timing ? record.time : "——"
      },
      {
        key: "sex",
        title: "性别",
        align: "center",
        width: 50,
        render: (text, record) => {
          return record.sex === "0" ? "未知" : record.sex === "1"  ? "男" : record.sex === "2" ? "女" : "全部";
        }
      },
      {
        key: "title",
        title: "标题",
        align: "center",
        width: 100,
        render: (text, record) => {
          let title = record.contents.length !== 0 ? record.contents[0].title : "";
          return <Tooltip placement="top" title={title}>
            <p className="textEllipsis" style={{width: 100}}>{title}</p>
          </Tooltip>
        }
      },
      {
        key: "description",
        title: "详细信息",
        align: "center",
        width: 100,
        render: (text, record) => {
          let description = record.contents.length !== 0 ? record.contents[0].description : "";
          return <Tooltip title={description}>
            <p className="textEllipsis" style={{maxWidth: 100}}>{description}</p>
          </Tooltip>
        }
      },
      {
        key: "url",
        title: "链接",
        align: "center",
        width: 70,
        render: (text, record) => {
          let url = record.contents.length !== 0 ? record.contents[0].url : "";
          return (
            <Tooltip title={url}>
              <p className="textEllipsis" style={{width: 70}}>{url}</p>
            </Tooltip>
          )
        }
      },
      {
        key: "img",
        title: "图片",
        width: 100,
        align: "center",
        render: (text, record) => (
          <img style={{width: "100%",}} src={record.img} alt=""/>
        )
      },
      {
        title: "Action",
        align: "center",
        render: (text, record, index) => (
          <div>
            <Button style={{marginRight: 5}} size="small" type="dashed" onClick={() => copyMessage(record)}>复制</Button>
            <Button style={{marginRight: 5}} size="small" type="primary">
              <Link to={{pathname: "./update", query: {...record}}}>修改</Link>
            </Button>
            <Button style={{marginRight: 5}} size="small" onClick={() => sendKefuMessage(record._id, record.tagId)}>发送消息</Button>
            <Button size="small" type="danger" onClick={() => deleteMessage(record._id, index)}>删除</Button>
          </div>
        )
      }
    ];
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <div className="fr">
            <RangePicker showTime onChange={changeDate} />
            <Button style={{margin: "0 0 20px 10px"}} type="danger" onClick={() => deleteByDate(startTime, endTime)}>按时间段删除</Button>
          </div>
          <Button type="primary"><Link to="./create">新建客服消息</Link></Button>
          <Table bordered size="small" rowKey={record => record._id} columns={kefuMessageHeader} dataSource={kefuMessageList} />
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  kefuMessageList: state.message.kefu.kefuMessageList
});

const mapDispatch = dispatch => ({
  getKefuMessageList() {
    dispatch(actionCreators.getKefuMessageList())
  },
  copyMessage(record) {
    dispatch(actionCreators.createKefuMessage(record))
  },
  deleteMessage(id, index) {
    dispatch(actionCreators.deleteMessage(id, index))
  },
  sendKefuMessage(id, tagId) {
    dispatch(actionCreators.sendKefuMessage(id, tagId))
  },
  deleteByDate(startTime, endTime) {
    dispatch(actionCreators.deleteByDate(startTime, endTime))
  },
  changeDate(date) {
    startTime = +date[0]._d;
    endTime = +date[1]._d;
  }
});

export default connect(mapState, mapDispatch)(Kefu);