import React, { Component, Fragment } from 'react';
import { Button,Icon,Modal,Table,Input,Select,Row,Col } from 'antd'

const { Option } = Select;

class AddRules extends Component {
  constructor(props){
    super(props)
    this.state = {
      visible: this.props.v,
      columsData: [{
        protocol:'TCP',
      }]
    }
  }
  porotcolSelect(r,i){
    return(
      <Select
        defaultValue="TCP"
        value={r.protocol}
        style={{ width: 120 }}
        onChange={value => {
          console.log(value)
          this.setState(state => {
            state.columsData[i].protocol = value;
            return {
              columsData: state.columsData
            }
          })
        }}
      >
        <Option value="TCP">TCP</Option>
        <Option value="UDP">UDP</Option>
        <Option value="HTTP">HTTP</Option>
      </Select>
    )
  }
  originIput(){
    return(
      <div></div>
    )
  }

  handleOk = e => {
    this.props.hideModal()
  };

  handleCancel = e => {
    this.props.hideModal()
  };

  addOneRule = e =>{
    this.setState((state,props)=>{
      state.columsData.push({})
      return{
        columsData:state.columsData
      }
    })
  }

  render() {

    const columns = [{
      title: '协议',
      dataIndex: 'protocol',
      key: 'protocol',
      render:(t,r,i)=>this.porotcolSelect(r,i),
      width:180
    },{
      title: '端口范围/ICMP类型',
      dataIndex: 'type',
      key: 'type',
      render:()=><Input placeholder='例如：22或22-30' />,
      width:180
    },{
      title: '源地址',
      dataIndex: 'origin',
      key: 'origin',
      width:300
    },{
      title: '描述',
      dataIndex: 'desc',
      key: 'desc',
      render:()=><Input />,
      width:220
    },{
      title: '操作',
      dataIndex: 'operation',
      key: 'operation',
      render:()=><Button type='link'>删除</Button>
    },]
    return (
      <Fragment>
        <Modal
          title="添加规则"
          visible={true}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width={1000}
        >
          <Table
            columns={columns}
            dataSource={this.state.columsData}
            rowKey={(r,i)=>i}
            pagination={false}
          />
          <Row style={{marginTop:'10px'}}>
            <Col span={12} offset={6}>
              <Button type='link' onClick={this.addOneRule}>
                <Icon type='plus-square' style={{color:'#1890ff'}}/>
                添加一条规则
              </Button>
              本批量添加规则页面，您还可以增加{9}条规则
            </Col>
          </Row>
        </Modal>
      </Fragment>
    );
  }
}

export default AddRules;