import React, {PureComponent} from 'react';
import BreadCrumb from "../../../../common/bread";
import {Button, Table, Tooltip, Modal, Radio, Switch, DatePicker, TimePicker} from "antd";
import moment from 'moment';
import {Link} from 'react-router-dom';
import {connect} from "react-redux";
import { actionCreators } from '../store';

class groupMessage extends PureComponent {
  componentWillMount() {
    const { code } = this.props.match.params;
    this.props.getMessageList(code);
    this.props.getTagList(code);
  }

  render() {
    const { messageList, match, openModal, sendModal, sendMessage, sendInfo, tagList, closeModal, changeTagId, sendTimingModal, sendTimingMessage, sendTimingInfo, openTimingModal, changeTimingState, copy, changeTime, deleteMaterial, clearMaterial }  = this.props;
    const { code } = match.params;
    const messageHeader = [
      {
        key: "update_time",
        title: "素材上传时间",
        align: "center",
        render: (text, record) => {
          let time = moment(record.update_time * 1000).format("YYYY-MM-DD, HH:mm:ss");
          return (
            <span>{time}</span>
          )
        }
      },
      {
        title: "图文标题",
        align: "center",
        width: 300,
        render: (text, record) => {
          let arr =
            record.content.news_item.length !== 0
              ? record.content.news_item
              : [];
          let titles = [];
          for (let i = 0; i < arr.length; i++) {
            titles.push(i + 1 + "." + arr[i].title);
          }
          return (
            <Tooltip placement="top" title={titles}>
              <p className="textEllipsis" style={{width: 300}}>{titles}</p>
            </Tooltip>
          )
        }
      },
      {
        key: "isTiming",
        title: "是否定时",
        align: "center",
        render: (text, record) => {
          let time = record.isTiming ? "是" : "否";
          return (
            <span>{time}</span>
          )
        }
      },
      {
        title: "标签",
        align: "center",
        render: (text, record) => {
          let tag = "无";
          if(record.tagId) {
            tag = tagList.map(item => {
              return (record.tagId === item.id) && (tag = item.name);
            });
          }
          return (
            <div>
              {tag}
            </div>
          )
        }
      },
      {
        key: "timing",
        title: "定时时间",
        align: "center",
        render: (text, record) => {
          let time = "~~";
          if (record.timing) {
            time = moment(record.timing).format("YYYY-MM-DD, HH:mm:ss")
          }
          return (
            <span>{time}</span>
          )
        }
      },
      {
        title: "Action",
        align: "center",
        width: 300,
        render: (text, record, index) => (
          <div>
            <Button size="small" style={{marginRight: 5}} type="dashed" onClick={() => openModal(record)}>发送消息</Button>
            <Button size="small" style={{marginRight: 5}} onClick={() => openTimingModal(record)}>定时发送</Button>
            <Button size="small" style={{marginRight: 5}} type="primary" onClick={() => copy(record._id)}>复制</Button>
            <Button size="small" type="danger" onClick={() => deleteMaterial(record._id, index)}>删除</Button>
          </div>
        )
      }
    ];
    const selectTagRadio = tagList.map(item => (
      <Radio key={item._id} value={item.id}>{item.name}</Radio>
    ));
    return (
      <div>
        <BreadCrumb/>
        <div className="container">
          <Button className="fr" style={{marginBottom: 20}} type="danger" onClick={() => clearMaterial(code)}>清空列表</Button>
          <Button style={{background: "#15F558", color: "#fff"}}><Link to={"../history/" + code}>群发历史</Link></Button>
          <Table bordered size="small" rowKey={record => record._id} columns={messageHeader} dataSource={messageList} />
        </div>
        <Modal
          title="请选择要发送的标签"
          visible={sendModal}
          onOk={() => sendMessage(sendInfo)}
          onCancel={closeModal}
        >
          <Radio.Group value={sendInfo.tagId} onChange={e => changeTagId(e, false)}>
            {selectTagRadio}
          </Radio.Group>
        </Modal>
        <Modal
          title="创建定时发送任务？"
          visible={sendTimingModal}
          onOk={() => sendTimingMessage(sendTimingInfo, code)}
          onCancel={closeModal}
        >
          <label htmlFor="tagId">
            标签：
            <Radio.Group id="tagId" value={sendTimingInfo.tagId} onChange={e => changeTagId(e, true)}>
              {selectTagRadio}
            </Radio.Group>
          </label>
          <label style={{display: "block", margin: "20px 0"}} htmlFor="isTiming">
            是否定时：
            <Switch checked={sendTimingInfo.isTiming} onChange={changeTimingState} />
          </label>
          <label htmlFor="timing" style={{display: sendTimingInfo.isTiming ? "block" : "none"}}>
            时间选择：
            <DatePicker
              value={moment(new Date(sendTimingInfo.timing), 'YYYY-MM-DD')}
              onChange={(time, timeString) => changeTime(timeString, "date")}
            />
            <TimePicker
              value={moment(new Date(sendTimingInfo.timing), 'HH-MM-SS')}
              onChange={(time, timeString) => changeTime(timeString, "time")}
            />
          </label>
        </Modal>
      </div>
    )
  }
  componentWillUnmount() {
    this.props.resetState()
  }
}


const mapState = state => ({
  tagList: state.message.group.tagList,
  messageList: state.message.group.messageList,
  sendInfo: state.message.group.sendInfo,
  sendTimingInfo: state.message.group.sendTimingInfo,
  sendModal: state.message.group.sendModal,
  sendTimingModal: state.message.group.sendTimingModal
});

const mapDispatch = dispatch => ({
  getTagList(code) {
    dispatch(actionCreators.getTagList(code))
  },
  getMessageList(code) {
    dispatch(actionCreators.getMessageList(code))
  },
  openModal(record) {
    dispatch(actionCreators.openModal(record));
  },
  sendMessage(sendInfo) {
    dispatch(actionCreators.sendMessage(sendInfo));
  },
  changeTagId(e, isTiming) {
    dispatch(actionCreators.changeTagId(e.target.value, isTiming));
  },
  openTimingModal(record) {
    dispatch(actionCreators.openTimingModal(record));
  },
  closeModal() {
    dispatch(actionCreators.closeModal());
  },
  sendTimingMessage(sendTimingInfo, code) {
    dispatch(actionCreators.sendTimingMessage(sendTimingInfo, code));
  },
  changeTimingState(value) {
    dispatch(actionCreators.changeTimingState(value));
  },
  changeTime(time, type) {
    dispatch(actionCreators.changeTime(time, type));
  },
  copy(id) {
    dispatch(actionCreators.copy(id));
  },
  deleteMaterial(id, index) {
    dispatch(actionCreators.deleteMaterial(id, index));
  },
  clearMaterial(code) {
    dispatch(actionCreators.clearMaterial(code));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  }
});

export default connect(mapState, mapDispatch)(groupMessage);